* {
  font-family: -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
  padding:60px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input{
  color: #333333;
  font-size: 1.3rem;
  padding:3px;
  width:100%;
}



.gifs-container{
  background-color: rgba(75, 160, 240, 0.055);
  text-align: center;
}


.category-title{

  font-size: 2rem;
  margin: 20px auto 10px;
}

.card{
  align-items: center;
  display: inline-flex;
  flex-direction: column;
  border: 2px solid grey;
  border-radius:5px;
  margin: 10px;
  overflow: hidden;
  
}
.card>img{
  min-height: 300px;
  max-height: 300px;
  border-radius:2px;
}

